* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  width: 200px;
  padding: 16px;
  border-radius: 5px;
  border: 1px solid #f3f3f3;
  background-color: white;
  text-align: center;
  margin: 0 auto;
}

.imac img {
  margin-top: 16px;
  width: 160px;
  height: 134px;
}

.card__title {
  color: #060b35;
  text-align: left;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  margin-top: 36px;
}

.card__code {
  font-size: 10px;
  font-weight: 400;
  text-align: left;
  color: #616070;
  margin-top: 4px;
}

.stars {
  display: flex;
  position: relative;
  margin-top: 17px;
}

.stars__star {
  background-image: url('../images/star.svg');
  background-repeat: no-repeat;
  background-position: center;
  height: 16px;
  width: 16px;
  margin: 0 2px;
}

.stars__star:first-child {
  margin-left: 0;
}

.stars--1 .stars__star:nth-child(-n + 1),
.stars--2 .stars__star:nth-child(-n + 2),
.stars--3 .stars__star:nth-child(-n + 3),
.stars--4 .stars__star:nth-child(-n + 4),
.stars--5 .stars__star:nth-child(-n + 5) {
  background-image: url('../images/star-active.svg');
}

.card__review {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  color: #060b35;
  margin-left: auto;
  position: relative;
  left: 59px;
  top: -14px;
}

.card__price {
  font-size: 12px;
  font-weight: 400;
  color: #616070;
  position: relative;
  top: 1px;
  left: -68px;
  margin-top: 11px;
}

.card__element {
  font-size: 16px;
  font-weight: 700;
  color: #060b35;
  position: relative;
  left: 58px;
  top: -16px;
}

.card__buy {
  background-color: #00acdc;
  width: 166px;
  height: 40px;
  border-radius: 6px;
  text-align: center;
  text-decoration: none;
  border: none;
  color: white;
  font-size: 14px;
  line-height: 40px;
  font-weight: 700;
  display: inline-block;
}

.card__buy:hover {
  background-color: white;
  border: 1px solid #00acdc;
  color: #00acdc;
}
